.App {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0px;
}

.bodyItems {
  margin: 0px 12%;
}
.headerBody {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	background: lightslategray;
	margin: 0px;
}

.logo {
	-webkit-flex: 1 1;
	        flex: 1 1;
	font-size: 2em;
}

.menuItems {
	-webkit-flex: 3 1;
	        flex: 3 1;
}

.items {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.item {
	-webkit-flex: 1 1;
	        flex: 1 1;
	list-style: none;
	text-align: center;
	font-size: 1.2em;
}

.item:hover {
	color: white;
	cursor: pointer;
}
.kantoorContent {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.intro {
	
}

.participants {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.participant {
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	max-width: 45%;
}

.name {
	color: lightblue;
	font-size: 1.8em;
	text-align: center;
}

.partText {
	text-align: justify;
}
.body {
	margin: 20px 0px 20px 0px;
	padding: 3px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
}

.subjectHeader {
	color: #008edb;
	font-size: 2em;
	text-align: left;
}
.RGcontent {
	width: 100%;
	margin-top: 20px;
}

.catsAndExpl {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.cats {
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.category {
	background: #008edb;
	color: white;
	padding: 5px;
	-webkit-align-self: center;
	        align-self: center;
	width: 300px;
	height: 80px;
	text-align: center;
	vertical-align: middle;
	font-size: 1.8em;
	margin: 10px;
}

.category:hover {
	background: #00476d;
	cursor: pointer;
}

.expl {
	-webkit-flex: 1 1;
	        flex: 1 1;
}

